import React, { useEffect , useState  }  from 'react'

import Select from 'react-select'
import './AddDestination.css'

function AddDestination() {



    const [selectedOption, setselectedOption] = useState("")

    const options = [
        { value: 'TREK', label: 'TREK' },
        { value: 'CAMP', label: 'CAMP' },
        { value: 'VISIT', label: 'VISIT' },
      ];

    let  handleChange = (selectedOption) => {
        
        console.log(`Option selected:`, selectedOption);
        setsubmission({...submission,type:selectedOption.value})
      };

    const [submission, setsubmission] = useState(

        {
            name:"",
            type:"",
            location:"",
            short_desc:"",
            main_desc:"",
            price:"",
            offer:"",
            pic:"",
            loc_code:"",
            status:"",
            ratings:""
            
        
            
        
        }

    )

   

    const submitCall = async (submission_Data) =>{

        let response = await fetch('http://localhost:5000/destinations/add', {
            method: 'POST',
            headers : { 
                'Content-Type': 'application/json'
             
               },
            
            body: JSON.stringify(submission_Data)
          });

          let result = await response.json();
          console.log("=======================")
          console.log(result)
          console.log(result)
    }

    const submitHandler = (e) => {
        e.preventDefault()
        console.log("Submit clicked")
        console.log(submission.name)
        console.log(submission.type)
        console.log(submission.location)
        console.log(submission.short_desc)
        console.log(submission.main_desc)
        console.log(submission.price)
        console.log(submission.offer)
        console.log(submission.pic)
        console.log(submission.loc_code)
        console.log(submission.status)
        console.log(submission.ratings)

        let submission_Data={
            name:submission.name,
            type:submission.type,
            location:submission.location,
            short_desc:submission.short_desc,
            main_desc:submission.main_desc,
            price:submission.price,
            offer:submission.offer,
            pic:submission.pic,
            loc_code:submission.loc_code,
            status:submission.status,
            ratings:submission.ratings,
        }

        console.log(submission_Data)

       

          submitCall(submission_Data)

         

    }

    return (
        <div className="add-panel card">
            
            <h3 className="Panel-Heading">Add Destination</h3>
            <form onSubmit={e => submitHandler(e)}>
            

            <div style={{display:"flex",flexWrap: "wrap"}}>

                <div  className="search-bar">
                    <span className="search-label">Destination Id</span>
                    <input className="input-field" placeholder="Name" type="text" onChange={ e => setsubmission({...submission,name:e.target.value})} value={submission.name}/>
                </div>

                <div className="search-bar">
                <span className="search-label">Type</span>
                   
                    <Select value={selectedOption.value} onChange={handleChange} options={options} />

                </div>

                <div className="search-bar">
                <span className="search-label">Location</span>
                    <input className="input-field" placeholder="Location" type="text" onChange={ e => setsubmission({...submission,location:e.target.value})} value={submission.location}/>
                   
                </div>

                <div className="search-bar">
                <span className="search-label">Short Desc</span>
                    <input className="input-field" placeholder="short desc" type="text" onChange={ e => setsubmission({...submission,short_desc:e.target.value})} value={submission.short_desc}/>
                   
                </div>
                
                <div className="search-bar">
                <p className="search-plabel">Price</p>
                <input className="input-field" style={{marginTop:"-50px"}} placeholder="Price" type="text" onChange={ e => setsubmission({...submission,price:e.target.value})} value={submission.price}/>
                   
                </div>
               
                <div className="search-bar">
                <p className="search-plabel">Offer</p>
                    <input className="input-field" style={{marginTop:"-50px"}} placeholder="offer" type="text" onChange={ e => setsubmission({...submission,offer:e.target.value})} value={submission.offer}/>
                   
                </div>
                <div className="search-bar">
                <p className="search-plabel">Picture</p>
                <input className="input-field" style={{marginTop:"-50px"}} placeholder="pic" type="text" onChange={ e => setsubmission({...submission,pic:e.target.value})} value={submission.pic}/>
               
                </div>
                <div className="search-bar">
                <p className="search-plabel">Location Code</p>
                <input className="input-field" style={{marginTop:"-50px"}} placeholder="loc_code" type="text" onChange={ e => setsubmission({...submission,loc_code:e.target.value})} value={submission.loc_code}/>
               
                </div>
                <div className="search-bar">
                <p className="search-plabel">Status</p>
                <input className="input-field" style={{marginTop:"-50px"}} placeholder="status" type="text" onChange={ e => setsubmission({...submission,status:e.target.value})} value={submission.status}/>
               
                </div>
                <div className="search-bar">
                <p className="search-plabel">Rating</p>
                <input className="input-field" style={{marginTop:"-50px"}} placeholder="ratings" type="text" onChange={ e => setsubmission({...submission,ratings:e.target.value})} value={submission.ratings}/>
               
                </div>

                <div className="search-bar">
                <span className="search-label">Description</span>
                    <textarea className="input-field" style={{width:"500px",height:"100px"}} placeholder="Description" type="text" onChange={ e => setsubmission({...submission,main_desc:e.target.value})} value={submission.main_desc}/>
                   
                </div>


                
        
            </div>

            <button type="submit" style={{marginTop:"50px"}} className="btn btn-SUBMIT">Submit</button>

            </form>
        </div>
    )
}

export default AddDestination
