import React, { useEffect , useState  }  from 'react'
import Card from './../CardComponent/Card'
import './ListDestination.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTrash } from '@fortawesome/free-solid-svg-icons'
import { faCode } from '@fortawesome/free-solid-svg-icons'
import classNames from 'classnames'

function ListDestination() {

    const [paginate, setpaginate] = useState()
    const [focusColor, setfocusColor] = useState({decrementColor:"#ffffff",presentColor:"#ffffff",incrementColor:"#ffffff"})

    const [list, listSet] = useState([
        {
            name:"",
            type:"",
            location:"=",
            short_desc:"",
            main_desc:"",
            price:"",
            offer:" ",
            pic:"",
            loc_code:"",
            status:"",
            ratings:""
            
        }
    ])

    var focusClass = classNames({
        
        'background-color': 'green',
        'height':'300px',
       
      });

    const paginateIncrement = () =>{
        console.log("Increment clicked")
        setpaginate(paginate+2)
        fetchMyAPI(paginate+2)
       
    }

    const paginateDecrement = () =>{
        console.log("Decrement clicked")
        //setpaginate(paginate-2)
        if(paginate-2 > 1){
            setpaginate(paginate-2)
            fetchMyAPI(paginate-2)
        } else{
            setpaginate(2)
            fetchMyAPI(1)
        }
    }

    const paginateset = (name,value) =>{
        //console.log(name)
        //console.log(value)
        
        if(name === "Decrement"){
            console.log("Decrement")
            if(paginate-1 > 1){
                setpaginate(paginate-1)
                fetchMyAPI(paginate-1)
                console.log(paginate-1)
                setfocusColor({decrementColor:"#ffffff",presentColor:"#099BFF",incrementColor:"#ffffff"})
            } else if(paginate-1 === 1){
                console.log(paginate-1)
                fetchMyAPI(1)
                setfocusColor({decrementColor:"#099BFF",presentColor:"#ffffff",incrementColor:"#ffffff"})
            }
           
        } else if(name === "Present"){
            console.log("Present")
            setpaginate(paginate)
            fetchMyAPI(paginate)
            setfocusColor({decrementColor:"#ffffff",presentColor:"#099BFF",incrementColor:"#ffffff"})
        } else if(name === "Increment"){
            console.log("Increment")
            setpaginate(paginate+1)
            fetchMyAPI(paginate+1)
            setfocusColor({decrementColor:"#ffffff",presentColor:"#099BFF",incrementColor:"#ffffff"})
        }
    }

    async function fetchMyAPI(page) {
        let response = await fetch(`http://localhost:5000/list?page=${page}&limit=5`)
        response = await response.json()
        listSet(response.results)
        console.log(response.results)

        
        
      }

    useEffect(() => {
       
        setpaginate(2)
        fetchMyAPI(1)
      }, [])

    const deleteDestination = async (id) => {

        let response = await fetch(`http://localhost:5000/delete/${id}`,{method: "POST"})
        response = await response.json()
        console.log(response.results)
        fetchMyAPI(paginate)
        console.log(paginate)
        


    }

    const deleteSubmitHandler = (e) => {
        console.log("Delete clicked")
        deleteDestination(e.target.id)
        console.log('page')
        console.log(paginate)
        fetchMyAPI(paginate)
        
        
    }

    return (
        <div>
            <div className="Paginate-bar">
    
                <div><button type="button" className="paginate-btn btn"  onClick={paginateDecrement}>-</button></div>
                <div><button type="button" value={paginate -1} name="Decrement" style={{backgroundColor:`${focusColor.decrementColor}`,boxShadow: "2px 1px 2px 2px rgb(212, 211, 211)"}} className="paginate-btn btn" onClick={e => paginateset(e.target.name,e.target.value)}>{paginate -1}</button></div>
                <div><button type="button" value={paginate} name="Present" style={{backgroundColor:`${focusColor.presentColor}`,boxShadow: "2px 1px 2px 2px rgb(212, 211, 211)"}} className="paginate-btn btn" onClick={e => paginateset(e.target.name,e.target.value)}>{paginate}</button></div>
                <div><button type="button" value={paginate +1} name="Increment" style={{backgroundColor:`${focusColor.incrementColor}`,boxShadow: "2px 1px 2px 2px rgb(212, 211, 211)"}} className="paginate-btn btn" onClick={e => paginateset(e.target.name,e.target.value)}>{paginate +1}</button></div>
                <div><button type="button" className="btn paginate-btn" onClick={paginateIncrement}>+</button></div>
            </div>
            <div className="list-panel card">
            
           
            {list.map((item) => (
                <div key={item._id}>
           

                <div className="card-container">
                    <div className="float-layout">
                        <div className="card-image" >
                            <img  src={item.pic} alt="Card image cap" style={{width:"200px",height:"200px",boxShadow: "2px 2px 5px 2px rgb(212, 211, 211)"}} className="image-card"></img>
                            <div className="card-desc " style={{width:"500px",border:"none",boxShadow: "2px 2px 5px 2px rgb(212, 211, 211)"}}>
                                <div className="card-title">{item.name}<span> - {item.loc_code}</span>
                                <span className="btn-icon"><button id={item._id} className="delete-button btn btn-sm" onClick={e => deleteSubmitHandler(e)}>Delete</button></span>
                                <span className="btn-icon"><button id={item._id} className="delete-button btn btn-sm" >Edit</button></span>
                                
                                
                                </div>
                                <div className="text">{item.short_desc}</div>
                            </div>
                        </div>
                    </div>
                </div>
  </div>
              ))}

            
            </div>
        </div>
    )
}

export default ListDestination
