import React from 'react'
import './card.css'
import { Link } from 'react-router-dom'


function Card(props) {

  let detailID=1

    return (
        <div className="MAIN-PANEL">
        <Link to={`/detail/${detailID}`} style={{ textDecoration: 'none',color: 'black' }}> 
        <div className="panel card" style={{width:`${props.width}`}} >
        <img className="card-panel-img" src={props.pic} alt="Card image cap" style={{width:`${props.width}`}}></img>
        <div className="card-body">
         
          <p className="card-desc">{props.desc}</p>
          
        </div>
      </div>
      </Link>
        </div>
    )
}

export default Card
