import React from 'react'
import {useState} from 'react'

import DropDown from './../DropDownComponent/DropDown'

import logo from './art_1.png'
import './Jumbotron.css'

import { faHiking } from '@fortawesome/free-solid-svg-icons'
import { faCampground } from '@fortawesome/free-solid-svg-icons'
import { faMountain } from '@fortawesome/free-solid-svg-icons'
import { faHotel } from '@fortawesome/free-solid-svg-icons'
import { faSearch } from '@fortawesome/free-solid-svg-icons'
import { faTree } from '@fortawesome/free-solid-svg-icons'
import { faArrowAltCircleLeft } from '@fortawesome/free-solid-svg-icons'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import pic_1 from './kuttanad.jpg'
import Card from './../CardComponent/Card'

function Jumbotron() {

    const [isSearch,setisSearch]=useState(true)

    const onClickEvent = () =>{
        console.log("isSearch")
        setisSearch(!isSearch)
        console.log(isSearch)
    }

    return (
        <div className="logo-panel">
       
        <div className="jumbotron-panel flex ">

            { isSearch &&
<>
            <div className="button-pannel-style card">
                <button type="button" class="btn btn-main"><FontAwesomeIcon icon={faHiking} size="sm"/></button>
                <h6 className="button-label-style">Hike</h6>
            </div>
            <div className="button-pannel-style card ">
                <button type="button" class="btn btn-main"><FontAwesomeIcon icon={faCampground} size="sm"/></button>
                <h6 className="button-label-style">Camp</h6>
            </div>
            <div className="button-pannel-style card ">
                <button type="button" class="btn btn-main"><FontAwesomeIcon icon={faMountain} size="sm"/></button>
                <h6 className="button-label-style">Mountain</h6>
            </div>
            <div className="button-pannel-style card">
                <button type="button" class="btn btn-main"><FontAwesomeIcon icon={faHotel} size="sm"/></button>
                <h6 className="button-label-style">Hotel</h6>
            </div>
           
            <div className="button-pannel-style card">
                <button type="button" class="btn btn-main"><FontAwesomeIcon icon={faTree} size="sm"/></button>
                <h6 className="button-label-style">Forest</h6>
            </div>

            <div className="button-pannel-style card">
            <button type="button" class="btn btn-main" onClick={onClickEvent}><FontAwesomeIcon icon={faSearch} size="sm"/></button>
            <h6 className="button-label-style">Search</h6>
            </div>
</>
            }

            { !isSearch &&
                <>
            <DropDown/>

            

            <button className="btn-search btn" type="submit">Search</button>

            <div className="button-pannel-return  card">
            <button type="button" className="btn btn-main" onClick={onClickEvent}><FontAwesomeIcon icon={faArrowAltCircleLeft} size="sm"/></button>
            <h6 className="button-label-style">Main menu</h6>
            </div>
            </>
            }
        </div>


        


        <div className="info-panel card">
        
       


        <div className="flex-panel">

        <Card pic={pic_1} title="Kuttanad" desc="Hello there" width="310px"/>
        <Card pic={pic_1} title="Kuttanad" desc="Hello there" width="310px"/>
        <Card pic={pic_1} title="Kuttanad" desc="Hello there" width="310px"/>
        <Card pic={pic_1} title="Kuttanad" desc="Hello there" width="310px"/>
        <Card pic={pic_1} title="Kuttanad" desc="Hello there" width="310px"/>
        </div>


        </div>
        
        </div>
    )
}

export default Jumbotron
