import React, { useEffect , useState  }  from 'react'
import GetDestinations from './GetDestinations'
import AddDestination from './AddDestination'
import ListDestination from './ListDestination'
import './Crud.css'

function CrudComponent() {

    const [addDestination, setaddDestination] = useState(false)

    const [listDestination, setlistDestination] = useState(true)

    const clickAdd =() =>{
        setaddDestination(true)
        setlistDestination(false)
    }

    const clickList =() =>{
        setlistDestination(true)
        setaddDestination(false)
    }

    return (
        <div>
        <div className="button-panel ">
        <button type="button"  className="btn btn-Panel" onClick={clickAdd}>Add</button>
        <button type="button" className="btn btn-Panel" onClick={clickList}>List</button>
        </div>
           { addDestination && <AddDestination/> } 
           { listDestination && <ListDestination/> }
        </div>
    )
}

export default CrudComponent
