import React from 'react'
import './NavBar.css'
import logo from './art_2.png'
import { Link } from 'react-router-dom'



function NavBar() {
    return (
        <div className="flex">
           <div className="flex logo-styling"><img className="logo-img" src={logo} alt="Card image cap"></img><div className="logo-name">MillionSmiles</div></div>
           <div className="flex Nav-Left">
         
           <Link to="/" >
                <button  className="btn btn-SUB">HOME</button>
           </Link>

           <Link to="/gallery" >
                <button  className="btn btn-SUB">GALLERY</button>
           </Link>

           <Link to="/contact" >
                <button  className="btn btn-SUB">CONTACT</button>
           </Link>
           <Link to="/explore" >
                <button  className="btn  btn-EXPLORE">EXPLORE</button>
           </Link>
          
           
         

           </div>

        </div>
    )
}

export default NavBar
