import React, { Component } from 'react'
import Select from 'react-select'
import './DropDown.css'
import CustomSelect from './CustomSelect';

function DropDown() {

    const styles={
     
        select:{
          width:'100%',
          maxWidth:600,
          borderRadius:'70px',
          borderColor:'blue',
          borderWidth:'10px'


        }
      }
      
      const options=[
        {label:'React',value:'react'},
        {label:'ReactNative',value:'react-native'},
        {label:'JavaScript',value:'js'},
        {label:'CSS',value:'css'},
      ]

      const price=[
        {label:'3000 - 5000',value:'react'},
        {label:'5000 - 10000',value:'react-native'},
        {label:'10000 - 150000',value:'js'},
        {label:'20000 - 300000',value:'css'},
      ]


      
      function onChangeInput(value){
        console.log(value);
      }

  

    return (
        <div className="flex">
       
        <div style={{width:"300px",marginTop:"20px"}}>
            <Select className="Select-box" options={options} placeholder="Location" />
        </div>   
        <div style={{width:"300px",marginTop:"20px"}}>
            <Select className="Select-box" options={price} placeholder="Price"/>
        </div>
        
        </div>
    )
}

export default DropDown
