import React, { useEffect , useState  }  from 'react'
import Select from 'react-select'
import './GetDestination.css'



function GetDestinations() {

    const [inputlocid, setinputlocid] = useState("")

    const onChangeInput = (e) => {
        console.log(e.target.value)
        setinputlocid(e.target.value)
    }

    const submitHandler = (e) => {
        e.preventDefault()
        console.log("Submit clicked")
        console.log(inputlocid)
    }

    return (
        <div>

            GetDestinations

                <form onSubmit={e => submitHandler(e)}>

                    <div className="search-panel search-panel-align">
                        <div  className="search-bar">
                            <span className="search-label">Destination Id</span>
                            <input className="input-field" type="text" onChange={e => onChangeInput(e)} value={inputlocid}/>
                        </div>

                        <div className="search-bar">
                            <span className="search-label">Location</span>
                            <Select/>
                        </div>

                        <button type="submit"  className="btn btn-SUBMIT">Submit</button>
                
                    </div>
                </form>

        </div>
    )
}

export default GetDestinations
