import React, { useEffect , useState , useRef , useCallback }  from 'react'
import InfinteScroll from './InfinteScroll'
import Masony from 'react-masonry-component'
import './Gallery.css'
import Card from './../CardComponent/Card'

function InfiniteGallery() {
    
    const [pageNumber, setpageNumber] = useState(1);

   

    const {

        destination,
    hasMore,
    loading,
    error

      } = InfinteScroll(pageNumber)

    const observer = useRef()
    const lastDestinationElementRef = useCallback(node => {

    if (loading) return
    if(observer.current) observer.current.disconnect()
    observer.current = new IntersectionObserver(entries => {

      if(entries[0].isIntersecting && hasMore){
        console.log('Visible')
        setpageNumber(pageNumber =>pageNumber+1)
      }

    })

    if (node) observer.current.observe(node)

    console.log(node)
  },[loading,hasMore])

     

     const masonryOptions ={
        fitWidth:false,
        columnWidth:300,
        gutter:30,
        itemSelector:".photo-item"
    }


    return (
        <div>
     
    
     {/** 
      <Masony
      className={"photo-list"}
      elementType={"ul"}
      options={masonryOptions}
      disableImagesLoaded={false}
      updateOnEachImageLoad={false}
    >
      {destination.map((item) => (
        <li className={`photo-item`}>
          <img className="masonry-image" src={item.pic} alt="" />
        </li>
      ))}
    </Masony>

    */}


    <Masony
    className={"photo-list"}
    elementType={"ul"}
    options={masonryOptions}
    disableImagesLoaded={false}
    updateOnEachImageLoad={false}
  >
    {destination.map((item,index) => {

        if(destination.length === index+1){
           return( <li className={`photo-item`}>
                <img key={item._id} ref={lastDestinationElementRef} className="masonry-image" src={item.pic} alt="" />
             </li>)
          } else {
              
            return( <li className={`photo-item`}>
                <img key={item._id} className="masonry-image" src={item.pic} alt="" />
             </li>)
          }

    })}
  </Masony>


        </div>
    )
}

export default InfiniteGallery
