import React, { useEffect , useState  }  from 'react'
import Axios from 'axios'
import  Carousal from './../CarousalComponent/Carousal'
import './Home.css'
import hiking from './hiking.jpg'



function Home() {

    const [dataset, setdataset] = useState([]) 

    useEffect(() => {

        {/*
        Axios.get(`https://reqres.in/api/users/${detailID}`)
            .then( response => {
                setdetail(response.data.data.email)
                console.log(response.data.data.email)
            })

        */}

        Axios.get(`http://localhost:5000/search/TRENDING`)
            .then( response => {
                setdataset(response.data)
                
            })


    },[])




    return (
        <div>
          
        <div className="jumbotron ">
             <div className="home-panel-desc">
              
              <p className="text-main">Welcome to MillionSmiles </p>
              <p className="text-main">choose from dozens dream spots to visit ...</p>
             
             </div>
             <div><img className="home-panel-img" src={hiking} alt="Card image cap"></img></div>
        </div>
     

     
      <Carousal width="310px" data={dataset} title="Trending Spots"/>
      
      

        </div>
    )
}

export default Home
